import React, { Component } from 'react'
import { defaults } from 'lodash'
import { shell } from '../../util'

export class CommandLogger extends Component {
  componentDidMount () {
    let { options, cmd } = this.props

    this.log = this.refs.log

    if (cmd) {
      this.proc = shell({
        cmd,
        ...options,
        data: (content) => {
          log.add(content)
        }
      })
    }
  }

  componentDidUnmount () {
     this.proc && this.proc.kill()
  }

  render () {
    return (
      <log ref='log' scrollable />
    )
  }
}

export default CommandLogger



